import axios from 'axios';
import { createAsyncThunk } from '@reduxjs/toolkit';
import { toast } from 'react-hot-toast';

axios.defaults.baseURL = 'https://connections-api.herokuapp.com/';

//   'token'    'headers.common.Authorization'  
const setAuthHeader = token => {
  axios.defaults.headers.common.Authorization = `Bearer ${token}`;
};

//   'token'
const clearAuthHeader = () => {
  axios.defaults.headers.common.Authorization = '';
};

//   
export const register = createAsyncThunk(
  'auth/register', //  
  //  'credentials'  ,   
  async (credentials, thunkAPI) => {
    try {
      //   'https://connections-api.herokuapp.com/users/signup'  'credentials';
      const res = await axios.post('/users/signup', credentials); //      'res'

      setAuthHeader(res.data.token); //     'token'
      toast.success('Welcome to the Phonebook');
      return res.data; //  
    } catch (error) {
      toast.error('oops');
      return thunkAPI.rejectWithValue(error.message);
    }
  }
);

//   
export const logIn = createAsyncThunk(
  'auth/login', //  
  //  'credentials'    
  async (credentials, thunkAPI) => {
    try {
      //   'https://connections-api.herokuapp.com/users/signup'  'credentials';
      const res = await axios.post('/users/login', credentials); //      'res'

      setAuthHeader(res.data.token); //     'token'
      toast.success('Welcome to the Phonebook');
      return res.data; //  
    } catch (error) {
      return thunkAPI.rejectWithValue(error.message);
    }
  }
);

// 
export const logOut = createAsyncThunk('auth/logout', async (_, thunkAPI) => {
  try {
    await axios.post('/users/logout');
    clearAuthHeader();
  } catch (error) {
    return thunkAPI.rejectWithValue(error.message);
  }
});

export const refreshUser = createAsyncThunk(
  'auth/refresh',
  async (_, { getState, rejectWithValue }) => {
    const { auth } = getState();
    const persistedToken = auth.token;

    if (persistedToken === null) {
      return rejectWithValue('Unable to fetch user');
    }

    setAuthHeader(persistedToken);
    try {
      const { data } = await axios.get('/users/current');
      return data;
    } catch (error) {
      return rejectWithValue(error.message);
    }
  }
);
